@use '../../colors.scss' as *;

.whitePage {
    background: white;
}

.whitePage * {
    background: white;
}

.row {
    width: 100%;
}

.column {
    width: 49%;
    float: left;
}

.quad {
    text-align: center;
}

h1 {
    font-size: 17pt;
    font-family: 'DejaVuBold';
}

h3 {
    font-size: 12pt;
    color: $mid-dark;

    margin: 20px;
}

.page3 {
    padding-top: 60px;
}

.page3 h1 {
    color: $dark;
    
    margin-top: 20px;
    margin-bottom: 20px;
}

.TLDR {
    font-family: 'DejaVuBold';
    font-size: 12pt;
}